.darkTheme {
  background-color: black;
}
.darkTheme .overview {
  color: white;
}
.darkTheme .visitHomepage > .visitHomepageWidth > a {
  color: white;
}
.darkTheme .dataNumbersSymbol {
  color: white;
  outline: solid white 2px;
}
.darkTheme .producedByWidth h3 {
  color: white;
}
.darkTheme .producedByWidth .cards {
  margin-top: 2rem;
  color: white;
}
.darkTheme .producedByWidth .cards .producerCard > .producerLogo {
  background-color: white;
  border-radius: 50%;
}

.backdrop {
  height: 500px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  padding: 1.5rem;
  box-shadow: inset 0 -200px 100px -50px black;
}
.backdrop .width {
  height: 100%;
  max-width: 900px;
  margin: 0 auto;
}
.backdrop .width .posterWrapper {
  position: relative;
  top: 100%;
  transform: translate(0, -100%);
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  -moz-column-gap: 2rem;
       column-gap: 2rem;
}
.backdrop .width .posterWrapper .poster {
  background-position: center;
  background-size: cover;
  height: 200px;
  aspect-ratio: 2/3;
  box-shadow: 5px 2px 10px -2px black;
  border-radius: 10px;
}
.backdrop .width .posterWrapper .posterText {
  padding: 1rem;
  color: white;
  border-radius: 10px;
}
.backdrop .width .posterWrapper .posterText h2 {
  font-size: 2rem;
}

.genres {
  background-color: black;
  padding: 0 1.5rem;
}
.genres .genreItems {
  margin: 0 auto;
  max-width: 900px;
  display: flex;
  flex-direction: row;
  -moz-column-gap: 1rem;
       column-gap: 1rem;
  padding: 1rem 0;
  background-color: black;
  color: white;
}
.genres .genreSymbol {
  padding: 0.2rem 0.5rem;
  border-radius: 1rem;
  border: solid white 1px;
}

.overviewWidth {
  display: flex;
  justify-content: center;
  padding: 1.5rem;
}
.overviewWidth p {
  max-width: 900px;
}

.likeAndRate {
  padding: 1.5rem;
  display: flex;
  justify-content: center;
}
.likeAndRate .likeAndRateWidth {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  -moz-column-gap: 2rem;
       column-gap: 2rem;
  max-width: 900px;
  width: 100%;
}
.likeAndRate .likeAndRateWidth button {
  padding: 0.5rem;
}

.visitHomepage {
  padding: 1.5rem;
  display: flex;
  justify-content: center;
}
.visitHomepage .visitHomepageWidth {
  max-width: 900px;
  width: 100%;
}
.visitHomepage .visitHomepageWidth a {
  font-weight: bold;
  text-decoration: none;
  font-size: 1.5rem;
  color: black;
}

.dataNumbers {
  padding: 1.5rem;
  display: flex;
  justify-content: center;
}
.dataNumbers .dataNumbersWidth {
  max-width: 900px;
  width: 100%;
  display: flex;
  flex-direction: row;
  row-gap: 1rem;
  flex-wrap: wrap;
  -moz-column-gap: 1rem;
       column-gap: 1rem;
}
.dataNumbers .dataNumbersWidth .dataNumbersSymbol {
  border: solid black 1px;
  padding: 0.2rem 0.5rem;
  border-radius: 1rem;
}

.producedBy {
  padding: 1.5rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.producedBy .producedByWidth {
  max-width: 900px;
  width: 100%;
  display: flex;
  flex-direction: column;
  -moz-column-gap: 1rem;
       column-gap: 1rem;
}
.producedBy .producedByWidth .cards {
  display: flex;
  flex-direction: row;
  -moz-column-gap: 2rem;
       column-gap: 2rem;
}
.producedBy .producedByWidth .producerCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.producedBy .producedByWidth .producerCard .producerLogo {
  width: 100px;
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 100px;
}

.trailerDiv {
  height: 500px;
  display: flex;
  justify-content: center;
}
.trailerDiv .trailer {
  height: 500px;
  width: 900px;
}

.recommendationDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.darkTheme .likeAndRate {
  color: white;
}
.darkTheme .likeAndRate button {
  color: white;
  background-color: black;
  border: solid white 2px;
}
.darkTheme .likeAndRate button:hover {
  color: black;
  background-color: white;
  border: solid black 2px;
}
.darkTheme .backdrop button {
  background-color: black;
  color: white;
  outline: solid white 2px;
}
.darkTheme .backdrop button:hover {
  background-color: white;
  color: black;
  outline: solid black 1px;
}